import styles from './order-details.module.css';
import icon from '../../images/graphics.svg';
import PropTypes from 'prop-types';
import { ThreeDots } from 'react-loader-spinner';

export default function OrderDetails({orderNumber, loader}) {

  return (
    <div className={styles.container}>
      <h2 className='text text_type_digits-large mt-30 mb-8'>{orderNumber}</h2>
      <p className='text text_type_main-medium mb-15'> </p>
      {loader ? 
      <ThreeDots /> 
      :
      <div>
        <img src={icon} alt="" />
      </div> 
      }
      <p className='text text_type_main-default mt-15'>   </p>
      <p className='text text_type_main-default text_color_inactive mb-30'>    </p>
    </div>
  )
}

OrderDetails.propTypes = {
  orderNumber: PropTypes.number
}